.history {
  height: 317px;
  width: 668px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.thead {
  background-color: #eaeef3;
  height: 50px;
}

.thead tr {
  font-size: 10px;
  display: flex;
  text-transform: uppercase;
}

.thead tr th {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 50px;
  border: 1px solid #fff;
}

.tbody tr {
  display: flex;
}

.tbody tr td {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 50px;
  border: 1px solid #eaeef3;
  color: #b0b0b0;
}
